"use client"

import { useState } from "react";
import api from "../../lib/axios";
import { useRouter } from "next/navigation";

export default function RegisterPage() {
  const [form, setForm] = useState({ name: "", email: "", password: "" });
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  const handleChange = (e) => setForm({ ...form, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      await api.post("/register", form);
      router.push("/login");
    } catch (err) {
      alert(err.response?.data?.message || "Registration failed");
    }
    setLoading(false);
  };

  return (
    <div className="flex items-center justify-center min-h-screen">
      <div className="card w-96 bg-base-100 shadow-xl p-6">
        <h2 className="card-title mb-4">Sign Up</h2>
        <form onSubmit={handleSubmit} className="flex flex-col gap-3">
          <input type="text" name="name" placeholder="Name" className="input input-bordered" onChange={handleChange} />
          <input type="email" name="email" placeholder="Email" className="input input-bordered" onChange={handleChange} />
          <input type="password" name="password" placeholder="Password" className="input input-bordered" onChange={handleChange} />
          <button type="submit" className={`btn btn-primary ${loading ? "loading" : ""}`}>Register</button>
        </form>
      </div>
    </div>
  );
}
